.ImgBookView {
    background-color: var(--color-background);
    padding: 4rem;
    border-radius: 16px;
    justify-content: center;
    align-content: center;
}

.ImgBookView img {
    width: 300px;
    height: 400px;
}

@media (max-width: 1224px) {
    .ImgBookView {
        padding: 0;
    }
    .ImgBookView img {
        width: 250px;
        height: 300px;
    }
}