.ImgMidiaView {
    background-color: var(--color-background);
    padding: 2rem;
    border-radius: 16px;
    justify-content: center;
    align-content: center;
}

.ImgMidiaView img {
    width: 350px;
    height: 500px;
}

@media (max-width: 1400px) {
    .ImgMidiaView img {
        width: 350px;
        height: 500px;
    }
}
@media (max-width: 1100px) {
    .ImgMidiaView img {
        width: 350px;
        height: 500px;
    }
}
@media (max-width: 425px) {
    .ImgMidiaView img {
        width: 250px;
        height: 400px;
    }
}