:root {
  --color-background: #F1F1F1;
  --color-primary-black: #000;
  --color-green-light: #55C855;
  --color-green-dark: #368736;
  --color-link: #726F6F;
  --color-red: #b12828;

  font-size: 60%;
}

*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: sans-serif;
  color: var(--color-link);
}

body {
  margin: 0;
  background-color: var(var(--color-background));
}

a {
  cursor: pointer;
}