.ModalEditMidia {
  display: flex;
  flex-direction: row;

  padding: 1rem 3rem;
}
.ModalEditMidia .Img-Selection {
  background-color: white;
  width: 20vw;
  height: 48vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  border: 1px solid rgba(76, 76, 76, .3);
  border-radius: 12px;

  margin-right: 1rem;
}
.ModalEditMidia .Img-Selection .Img-Views  {
  height: 80%;
  width: 100%;
  padding: 1rem 0;
  overflow: scroll;
  overflow-x: hidden;
}

.ModalEditMidia .Img-Selection .Img-Views  img{
  width: 100px;
  height: 120px;
  border-radius: 20px;
  padding: 1rem;

}

.ModalEditMidia .Img-Selection .Img-InputSelection {
  background-color: black;
  color: white;
  height: 10%;
  width: 100%;
  border-radius: 0 0 10px 10px;

  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.ModalEditMidia .Img-Selection .Img-InputSelection label {
  background-color: #5ace5a;

  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: 500;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  /* display: block; */
  cursor: pointer;
}
.ModalEditMidia .Img-Selection .Img-InputSelection label input[type="file"]{
  display: none;
}

.ModalEditMidia .ValueData {
  display: flex;
  flex-direction: column;
}

.ModalEditMidia .ValueData select {
  -webkit-appearance: none;
  -moz-appearance: none;  
  appearance: none; 

  width: 280px; 
  height:40px;  

  margin: 0 0 1rem 0;
  padding: .5rem 7rem .5rem 1rem;

  background: url('../../../Images/IcoSelect.png') no-repeat #ffff;    
  background-position: 240px center; 
  background-size: 38px;
  
  border:1px solid rgba(76, 76, 76, .3);
  border-radius: 6px;

  font-size: 2rem;

  transition: .1s ease-in-out;
}

.ModalEditMidia .ValueData select:focus {
  font-size: 1.9rem;
  border: 2px solid rgba(76, 76, 76, .5);
  outline: 0;
}
.ModalEditMidia .ValueData select option {
  font-size: 2rem;
}

.ModalEditMidia .ValueData .checkbox {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
}

.ModalEditMidia .ValueData .checkbox input{
  cursor: pointer;
  all: unset;
  border: 1px solid rgba(76, 76, 76, .5);
  width: 18px;
  height: 18px;
  display: inline-block;
  border-radius: 10px;
  margin-bottom: 1px;
}

.ModalEditMidia .ValueData .checkbox input:checked{
  background-color: white;
  border: 6px solid  #55C855;
  width: 8px;
  height: 8px;
}

.ModalEditMidia .ValueData .checkbox p{
  font-size: 2rem;
  margin-left: 1rem;
}

.ModalEditMidia .ValueData > button{
  width: 25rem;
  height: 5rem;

  color: rgba(76, 76, 76, .8);

  border: 1px solid rgba(76, 76, 76, .5);
  border-radius: 10px;

  font-size: 20px;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;
  
  margin-left: 2.5rem;
}

.ModalEditMidia .ValueData > button:hover {
  background-color: var(--color-red);
  color: var(--color-background);
}

.ModalEditMidia .ValueData > button:last-child{
  width: 25rem;
  height: 5rem;
  
  background-color: var(--color-green-light);
  color: var(--color-background);

  border: none;
  border-radius: 10px;

  font-size: 20px;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;
  margin-top: 3rem;

}

.ModalEditMidia .ValueData > button:last-child:hover{
    background-color: var(--color-green-dark);
}
