.ProdutoCliente {
  overflow: hidden;
  height: 100vh;
}

.ProdutoCliente .Container .Top-Menu .CategoryMenu {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid rgba(114, 111, 111, 30%);
}

.ProdutoCliente .Container .Content-App {
  min-height: 100vh;
  background-color: #f1f1f1;
  width: 100vw;
  margin-bottom: 1rem;
}

.ProdutoCliente .Container .Content-App .content-midia-views {
  padding: 1rem 0 0;
  display: grid;
  grid-template-columns: repeat(5,1fr);
  grid-template-rows: 250px;
}

.ProdutoCliente .Container .Content-App .Content-Pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
  padding: 10px 0;

  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

@media (max-width: 1024px) {
    .ProdutoCliente .Container .Content-App .content-midia-views{
      grid-template-columns: repeat(4, 1fr);
      grid-auto-rows: minmax(250px, auto);
      margin-bottom: 4rem;
    }
}
@media (max-width: 768px) {
  .ProdutoCliente .Container .Content-App .content-midia-views{
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(250px, auto);
  }
}
@media (max-width: 550px) {
    .ProdutoCliente .Container .Content-App .content-midia-views{
      grid-template-columns: repeat(2, 1fr);
      grid-auto-rows: minmax(250px, auto);
    }
}
@media (max-width: 425px) {
    .ProdutoCliente .Container .Content-App .content-midia-views{
      grid-template-columns: repeat(1, 1fr);
      grid-auto-rows: minmax(250px, auto);
    }
}