.ModalViewMedia{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3rem;
}


.ModalViewMedia > img{
    height: auto !important;
    width: auto !important;
    max-width: 75vw;
    max-height: 75vh;
    padding: 0 !important;

    border: 1px solid rgba(0, 0, 0, .3);
    border-radius: 12px;
}




.ImgProdutoView .PrincipalImg{
    /* padding: 12px 4rem; */
    border-radius: 8px;
    
    background-color: white;
    /* width: 100%; */
    height: 200px;
    border: 1px solid rgba(0, 0, 0, .3);
    border-radius: 10px;
    padding: 1rem;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    overflow-y: scroll;
}

.ImgProdutoView > .PrincipalImg img {
    padding: 12px 4rem;
    width: 100px;
    height: 130px;
}

.ImgProdutoView span {
    display: flex;
    align-self: flex-start;
    font-size: 2rem;
    margin-top: 2rem;
}

.ImgProdutoView .Cor {
    padding: 12px 4rem;

    border-radius: 8px;
    
    background-color: white;
    height: 200px;
    border: 1px solid rgba(0, 0, 0, .3);
    border-radius: 10px;
    padding: 1rem;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    overflow-y: scroll;
}

.ImgProdutoView .Cor img {
    padding: 12px 4rem;
    width: 100px;
    height: 130px;
}

