.Modal-EditBook {
  padding: 2rem 3rem; 
}
.Modal-EditBook .form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-bottom: 1.5rem;
}

.Modal-EditBook .form .description {
  display: flex;
  flex-direction: column;
}

.Modal-EditBook .form .description  input {
  padding: .5rem 7rem .5rem 1rem;
  font-size: 15px;
  margin: 0 0 1rem 2.5rem;

  border: 1px solid rgba(76, 76, 76, .5);
}

.Modal-EditBook .form .description  textarea {
  padding: 1rem 2.25rem 3rem 1rem;
  height: 100%;
  font-size: 15px;
  margin: 0 0 0 2.5rem;

  border: 1px solid rgba(76, 76, 76, .5);

  resize: none;
}

.Modal-EditBook .form .MidiaSelection {
  display: flex;
  flex-direction: column;

  width: 400px;

  background-color: white;
  border: 1px solid rgba(76, 76, 76, .5);
  border-radius: 5px;
  
  margin-left: 1rem;
  
}
.Modal-EditBook .form .MidiaSelection .Area-ViewsMedias{
  height: 250px;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-template-rows: 80px;
  overflow: scroll;
  overflow-x: hidden;
}

.Modal-EditBook .form .MidiaSelection .Area-SelectMidias{
  background-color: #5ace5a;
  color: white;
  height: 35px;
  width: 100%;

  margin-top: 4px;

  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;

  border: none;
}

.Modal-EditBook .form .MidiaSelection button {
  padding: .5rem;
  margin-right: 0 !important;

  

  display: flex;

  border-radius: 100px;


  background-color: transparent;

}
.Modal-EditBook .form .MidiaSelection button:hover {
  transition: .5s;
  background-color: rgba(76, 76, 76, .1);
}
.Modal-EditBook .form .MidiaSelection button img{
  margin: 0;
}

.Modal-EditBook > button{
  width: 25rem;
  height: 5rem;

  color: rgba(76, 76, 76, .8);

  border: 1px solid rgba(76, 76, 76, .5);
  border-radius: 10px;

  font-size: 20px;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;
  
  margin-left: 2.5rem;
}
.Modal-EditBook > button:hover {
  background-color: var(--color-red);
  color: var(--color-background);
}

.Modal-EditBook > button:last-child{
  width: 25rem;
  height: 5rem;
  
  background-color: var(--color-green-light);
  color: var(--color-background);

  border: none;
  border-radius: 10px;

  font-size: 20px;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;

   margin-left: 4rem;
}
.Modal-EditBook > button:last-child:hover{
    background-color: var(--color-green-dark);
}