.ProductView {
    flex-direction: row;
    text-align: center;
    background-color: #f1f1f1;
    display: flex;
}

.ProductView .Container {
    margin-left: 20vw;
}

.ProductView .Container .ProductViewContent .Header {
    display: flex;
    margin-left: 2rem;
    margin-top: 2rem;
}

.ProductView .Container .ProductViewContent .ProductContainer {
    width: 35vw;
    margin-top: 2rem;
    margin-right: 2rem;
}

.ProductView .Container .ProductViewContent .ProductContent {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-left: 5rem;
}

.ProductView .Container .ProductViewContent .ProductContent .ProductInfo {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 3rem;
    width: 30vw;
    height: 100vh;
    display: flex;
}


.ProductView .Container .ProductViewContent .ProductContent .ProductInfo .legend {
    display: flex;
    justify-self: self-start;
    font-size: 1.8rem;
    font-weight: 400;
    margin-top: 1.5rem;
    margin-left: .5rem;
    margin-bottom: .5rem;
}

.ProductView h1 {
    font: 3.5rem 'Inter';
    margin-left: 45%;
}

.ProductView .Container .ProductViewContent .ProductContent .ProductInfo .input {
    height: 3rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px
}

.ProductView .Container .ProductViewContent .ProductContent .ProductInfo > select {
    width: 30vw;
    margin: 1rem 0;
}

.ProductView select {
    background-color: white;
    font-size: 2rem;
    border-radius: 0;
    overflow: hidden;
    width: 45%;
    height: 4rem;
    border-radius: 0.8rem;
}

.ProductView hr {
    margin-top: 2rem;
}

.ProductView .Container .ProductViewContent .ProductContent .ProductInfo hr{
    margin-top: 2rem;
}

.ProductView .Container .ProductViewContent .ProductContent .ProductInfo h2 {
    font: 2rem 'Inter';
    font-weight: 600;
    align-self: flex-start;
    margin-top: 2rem;
}

.ProductView .Container .ProductViewContent .ProductContent .ProductInfo .button {
    margin-top: 4rem;
    margin-bottom: 5rem;
    width: 60%;
    font: 400 2rem Roboto;
    background: var(--color-green-light);
    color: white;
    border-radius: .8rem;
    border: none;
    transition: .5s;
    align-self: center;
}

.ProductView .Container .ProductViewContent .ProductContent .ProductInfo .button:hover {
    background-color: var(--color-green-dark);
}

@media (max-width: 1800px) {
    .ProductView select {
        width: 45%;
    }
}

@media (max-width: 1070px) {
    .ProductView .Container .ProductViewContent .ProductContent {
        display: flex;
        flex-direction: column;
        margin-left: 1rem;
    }
    .ProductView .Container .ProductViewContent .ProductContent .ProductInfo {
        width: 90%;
    }
    .ProductView .Container .ProductViewContent .ProductContainer {
        width: 55vw;
    }
    .ProductView .Container .ProductViewContent .ProductContent .ProductInfo {
        width: 55vw;
        margin-left: 1rem;
    }
    .ProductView .Container .ProductViewContent .ProductContent .ProductInfo select {
        width: 55vw;
    }
    .ProductView .Container {
        margin-left: 30vw;
    }
    .ProductView h1 {
        margin-left: 35%;
    }
}

@media (max-width: 768px) {
    .ProductView .Container {
        margin-left: 0;
    }
    .ProductView h1 {
        font-size: 3rem;
        margin-left: 25%;
    }
    .ProductView .Container .ProductViewContent .ProductContent .ProductInfo .CategoriaContainer input {
        width: 80%;
    }
    .ProductView .Container .ProductViewContent .ProductContent .ProductInfo {
        height: 100%;
        margin-left: 20%;
        margin-bottom: 50px;
    }
    .ProductView .Container .ProductViewContent .ProductContent {
        width: 90vw;
    }
    .ProductView .Container .ProductViewContent .ProductContent .ProductContainer {
        margin-left: 20%;
        margin-right: 0;
    }
    .ProductView .button{
        display: none;
    }
}