.ForgotContainer {
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url(../../Images/background-image.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.ForgotContainer .ForgotContent {
    min-height: 30%;
    min-width: 30%;
    background: var(--color-primary-black);
    flex-direction: column;
    padding: 4rem;

    border-radius: 1rem;
    display: flex;
    /* align-items: center; */
}

.ForgotContainer .ForgotContent .LogoContainer {
    align-self: center;
}

.ForgotContainer .ForgotContent .FormContent {
    display: flex;
    flex-direction: column;
    margin-top: 3rem; 
}

.ForgotContainer .ForgotContent h1 {
    font: 400 2rem Roboto;
    color: var(--color-background);
}
.ForgotContainer .ForgotContent span {
    font: 400 1.5rem Roboto;
    color: var(--color-background);
}

.ForgotContainer .ForgotContent input {
    height: 3rem;
    margin-top: 2rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px
}

.ForgotContainer .ForgotContent .button {
    margin-top: 3rem;
    width: 30%;
    padding: 1rem;
    font: 400 2.5rem Roboto;
    background: var(--color-green-light);
    color: white;
    border-radius: .8rem;
    border: none;
    align-self: center;

    text-align: center;
    transition: 0.5s;
    margin-bottom: .5rem;
}

.ForgotContainer .ForgotContent .button:hover {
    background: var(--color-green-dark);
    text-decoration: none;
}

@media (max-width: 500px) {
    .ForgotContainer .ForgotContent .button{
        font-size: 1.5rem;
        width: 30%;
    }
    .ForgotContainer .ForgotContent .LogoContainer img {
        width: 50%;
    }
}