.container {
    padding: 1rem;

    justify-content: center;
    align-items: center;
}

.container .info{
    padding: 1rem;
    margin: 0;
}

.container .info span{
    display: flex;
    flex-direction: column;
}

.container .info span p{
    color: white;
    font-size: 15px;
}
.container .info span p:last-child {
    color: #726F6F;
    font-size: 11px;
    font-weight: 400;
    font-family: 'Inter';
}

@media (max-width: 500px) {
    .container img{
        width: 50%;
        margin-right: -9px;
    }
    .container .info span p {
        font-size: 1rem;
    }
    .container .info span p:last-child {
        font-size: 0.8rem;
    }
}