.navbar {
    margin-top: 2rem;
    margin-right: 1rem;
    z-index: 2;
}

.menu-bars {
    border: none;
    background: none;
}

.nav-menu {
    background-color: var(--color-primary-black);
    width: 50%;
    height: 100vh;
    display: flex;
    justify-content: flex-start;
    position: fixed;
    top: 0;
    right: -100%;
    transition: 850ms;
    flex-direction: column;
}

.nav-menu.active {
    right: 0;
    transition: 350ms;
}

.nav-menu button {
    margin-top: 2rem;
    margin-bottom: 3rem;
}

.nav-menu-item {
    font-size: 3rem;
    margin-left: 1rem;
}
