.MenuCategoryClt {
    display: flex;
    flex-direction: row;
}

.MenuCategoryClt .Title-MenuCategory {
    padding: 2rem 2rem;
    display: flex;
    align-items: center;
    font-size: 2.2rem;
}

.MenuCategoryClt .Title-MenuCategory img {
    padding: 0 0 0 1rem;
    width: 2rem;
}

.MenuCategoryClt .Content-MenuCategoryClt {
    position: relative;
    z-index: 100;
    
    background-color: #F1F1F1;
    border: 1px solid rgba(64, 64, 70, 0.068);
    top: 5.5rem;

    font-size: 5rem;
    padding: .5rem 4rem;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    position: fixed;
}