.NewPasswordContainer {
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url(../../Images/background-image.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.NewPasswordContainer .NewPasswordContent {
    min-width: 30%;
    min-height: 30%;
    background: var(--color-primary-black);
    flex-direction: column;
    padding: 4rem 2rem;

    border-radius: 1rem;
    display: flex;
    align-items: center;
}

.NewPasswordContainer .NewPasswordContent .LogoContainer {
    display: flex;
    justify-content: center;
}

.NewPasswordContainer .NewPasswordContent .FormContent {
    padding: 3rem;
    align-items: center;
    justify-content: center;
}

.NewPasswordContainer .NewPasswordContent h1 {
    font: 400 2rem Roboto;
    color: var(--color-background);
}

.NewPasswordContainer .NewPasswordContent input {
    width: 100%;
    height: 3rem;
    margin-top: 2rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px
}

.NewPasswordContainer .NewPasswordContent .button {
    margin-top: 3rem;
    width: 30%;
    padding: 1rem;
    font: 400 2.5rem Roboto;
    background: var(--color-green-light);
    color: white;
    border-radius: .8rem;
    border: none;

    text-align: center;
    transition: 0.5s;
    margin-bottom: .5rem;
}

.NewPasswordContainer .NewPasswordContent .button:hover {
    background: var(--color-green-dark);
    text-decoration: none;
}

@media (max-width: 500px) {
    .NewPasswordContainer .NewPasswordContent .button{
        font-size: 1.5rem;
        width: 30%;
    }
    .NewPasswordContainer .NewPasswordContent .LogoContainer img {
        width: 50%;
    }
}