.BookView {
    flex-direction: row;
    text-align: center;
    background-color: #f1f1f1;
}

.BookView .Container {
    margin-left: 20vw;
}

.BookView .Container .BookViewContent .Header {
    display: flex;
    margin-left: 2rem;
}

.BookView h1 {
    font: 3.5rem 'Inter';
    margin-left: 45%;
    margin-top: 2rem;
}


.BookView .Container .BookViewContent .Header a {
    margin-top: 2rem;
}

.BookView hr {
    margin-top: 2rem;
}

.BookView .Container .BookViewContent {
    align-items: center;
    justify-items: center;
}   

.BookView .Container .BookViewContent .BookContent{
    display: flex;
    flex-direction: row;
    width: 100%;
}

.BookView .Container .BookViewContent .BookContent .BookContainer {
    margin-top: 2rem;
    margin-left: 8rem;
    margin-right: 3rem;
}

.BookView .Container .BookViewContent .BookContent .BookContainer .book-midias{
    background-color: white;
    width: 550px;
    height: 200px;
    border: 1px solid rgba(0, 0, 0, .3);
    border-radius: 10px;
    padding: 1rem;

    display: flex;
    justify-content: flex-start;
    align-items: center;


    overflow: scroll;
    overflow-y: hidden;
}

.BookView .Container .BookViewContent .BookContent .BookContainer .book-midias img{
    width: 90px;
    height: 120px;
    border: 1px solid rgba(0, 0, 0, .2);
    border-radius: 15px;
    margin: 1rem;
    
}
.BookView .Container .BookViewContent .BookContent .BookContainer .book-midias img:first-child {
    margin-left: 2rem;
}

.BookView .Container .BookViewContent .BookContent .BookInfo {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 3rem;
    margin-right: 32px;
    width: 40vw;
    height: 100vh;

    display: flex;
}

.BookView input {
    height: 3rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px
}

.BookView textarea {
    padding: 1rem;
    height: 15rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px;
    resize: none;
}

.BookView .Container .BookViewContent .BookContent .BookInfo #titulo {
    display: flex;
    justify-self: self-start;
    font-size: 1.8rem;
    font-weight: 400;
    margin-top: 2rem;
}

.BookView .Container .BookViewContent .BookContent .BookInfo .description {
    display: flex;
    justify-self: self-start;
    font-size: 1.8rem;
    font-weight: 400;
    /* margin-top: 2rem; */
}

.BookView .Container .BookViewContent .BookContent .BookInfo .button {
    margin-top: 5rem;
    margin-bottom: 5rem;
    width: 50%;
    font: 400 2rem Roboto;
    background: var(--color-green-light);
    color: white;
    border-radius: .8rem;
    border: none;
    transition: .5s;
    align-self: center;
}

.BookView .Container .BookViewContent .BookContent .BookInfo .button:hover {
    background-color: var(--color-green-dark);  
}

.BookView .button:hover {
    background: var(--color-green-dark);
    text-decoration: none;
}

@media (max-width: 1224px) {
    .BookView .Container .BookViewContent .BookContent .BookContainer .book-midias{
        width: 450px;
        height: 200px;
        padding: 0.5rem;
    }
}

@media (max-width: 1024px) {
    .BookView .Container .BookViewContent .BookContent .BookContainer .book-midias{
        align-self: center;
    }
    .BookView .Container .BookViewContent .BookContent{
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .BookView .Container .BookViewContent .BookContent .BookContainer {
        margin-top: 2rem;
        margin-left: 8rem;
        margin-right: 2rem;
        align-self: center;
        justify-content: center ;
        display: flex;
        flex-direction: column;
    }
    .BookView .Container .BookViewContent .BookContent .BookInfo {
        display: flex;
        align-self: center;
        flex-direction: column;
        width: 60vw;
        height: 60vh;
    }
}

@media (max-width: 768px) {
    .BookView .Container {
        margin-left: 0;
    }
    .BookView .Container .BookViewContent .BookContent .BookInfo {
        display: flex;
        align-self: center;
        margin-left: 0;
        margin-right: 0;
        width: 90vw;
    }
    .BookView .Container .BookViewContent .BookContent .BookContainer .book-midias{
        width: 250px;
        height: 100px;
        padding: 0.5rem;
    }
    .BookView .Container .BookViewContent .BookContent .BookContainer {
        margin-left: 0;
        margin-right: 0;
    }
    .BookView h1 {
        margin-left: 35%;
    }
    .BookView .button{
        display: none;
    }
}