.OrderBy {
    /* margin-top: 10px; */

    border-top: 1px solid rgba(114, 111, 111, 30%);
    border-bottom: 2px solid rgba(114, 111, 111, 30%);

    display: flex;
    justify-content: space-around;
    /* align-items: center; */
    justify-content: center;
}

.OrderBy select {
    margin-right: 10px;
    width: 240px;
    height: 55px;
    font-size: 2rem;
    border: none;
    border-left: 1px solid rgba(114, 111, 111, 30%);
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 1.2rem;
    padding: 0 1.2rem;
}
.OrderBy select:focus {
    box-shadow: 0 0 0 0;
    outline: 0;
}
.OrderBy select:first-child {
    border-left: none
}

.OrderBy select option {
    font-size: 2rem;
}

@media (max-width: 840px) {
    .OrderBy select {
        font-size: 1.5rem;
    }
}

@media (max-width: 768px) {
    .OrderBy {
        display: flex;
        flex-direction: column;
    }
    .OrderBy select:first-child {
        border-left: 1px solid rgba(114, 111, 111, 30%);
    }
    .OrderBy select {
        font-size: 2rem;
    }
}

@media (max-width: 512px) {
    .OrderBy select {
        font-size: 1.5rem;
        width: auto;
        margin: 0;
        align-items: center;
    }
}