#page-access {
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url(../../Images/background-image.png);
    background-repeat: no-repeat;
    background-size: cover;
}

#page-access .container {
    min-width: 40%;
    min-height: 40%;
    background: var(--color-primary-black);
    flex-direction: column;
    padding: 4rem 2rem;

    border-radius: 1rem;
    display: flex;
}

#page-access .container .logo-container {
    display: flex;
    justify-content: center;
}

#page-access .container .access-content {
    display: flex;
    flex-direction: row;
    margin-top: 5rem;
}

#page-access .container .access-content a {
    border: none;
    background: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
}

#page-access .container .access-content .convidado {
    display: flex;
    align-items: center;
    justify-content: center;
}

#page-access .container .access-content img {
    width: 100%;
}

#page-access .container .access-content .convidadoimg {
    border: 5px solid var(--color-green-light);
    padding: 4rem;
    border-radius: 50%;
}

#page-access .container .access-content span {
    font: bold 3rem Inter;
    color: var(--color-background);
    margin-top: 1rem;
}

#page-access .container .access-content .gerente {
    display: flex;
    align-items: center;
    margin-left: 10rem
}

#page-access .container .access-content .gerenteimg {
    border: 5px solid var(--color-green-light);
    background-color: var(--color-green-light);
    padding: 4rem;
    border-radius: 50%;
}

@media (max-width: 500px) {
    #page-access .container .access-content .convidadoimg {
        border: 2px solid var(--color-green-light);
        padding: 3rem;
        border-radius: 50%;
    }
    #page-access .container .access-content .gerenteimg {
        border: 2px solid var(--color-green-light);
        background-color: var(--color-green-light);
        padding: 3rem;
        border-radius: 50%;
    }
    #page-access .container .access-content img {
        width: 100%;
    }
    #page-access .container .access-content span {
        font: bold 2rem Inter;
        color: var(--color-background);
    }
    #page-access .container .access-content {
        flex-direction: column;
    }
    #page-access .container .access-content .gerente {
        margin-left: 0;
        margin-top: 2rem;
    }
}
