.BookPage {
  display: flex;
  flex-direction: row;
  text-align: center;
}

.BookPage .Container {
  margin-left: 20vw;
  min-height: 100vh;
}

.BookPage .Container .Top-Menu .CategoryMenu {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid rgba(114, 111, 111, 30%);
}

.BookPage .Container .Top-Menu {
  position: fixed;
  top: 0;
  width: 80vw;
  background-color: white;
}

.BookPage .Container .Content-App {
  background-color: #f1f1f1;
  width: 80vw;
  min-height: 100vh;
}

.BookPage .Container .Content-App .content-midia-views {
  padding: 80px 0 0;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-template-rows: 270px;
  align-items: center;
  justify-content: center;
}

.BookPage .Container .Content-App .Content-Pagination{
  margin-left: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
  padding: 5px 0;

  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}


@media (max-width: 1440px) {
  .BookPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(200px, auto);
  }
}

@media (max-width: 1200px) {
  .BookPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(3, 250px);
    grid-auto-rows: minmax(250px, auto);
    margin-bottom: 5.5rem;
  }
}

@media (max-width: 830px) {
  .BookPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(2, 220px);
    grid-auto-rows: minmax(250px, auto);
  }
}

@media (max-width: 768px) {
  .BookPage .Container .Top-Menu{
    width: 100vw;
  }
  .BookPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(3, 200px);
    width: 100vw;
  }
  .BookPage .Container {
    margin-left: 0;
  }
  .BookPage .Container .Top-Menu .CategoryMenu {
    margin-left: 0;
  }
  .BookPage .Container .Content-App {
    margin-top: 70px;
    margin-left: 0;
    width: 100vw;
  }
  .BookPage .Container .Content-App .Content-Pagination {
    margin-left: 0;
  }
  .BookPage .Container .Content-App .content-midia-views {
    padding: 10px 0 9rem 0;
  }
}

@media (max-width: 600px) {
  .BookPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(2, 200px);
    grid-auto-rows: minmax(250px, auto);
  }
  .BookPage .Container .Content-App {
    margin-left: 0 0 0;
  }
}

@media (max-width: 490px) {
  .BookPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(1, 200px);
    grid-auto-rows: minmax(250px, auto);
  }
  .BookPage .Container .Content-App {
    margin-left: 0;
  }
}


