.MenuCategory {
    display: flex;
    flex-direction: row;
}

.MenuCategory .Title-MenuCategory {
    padding: 2rem 2rem;
    display: flex;
    align-items: center;
    font-size: 2.2rem;
}

.MenuCategory .Title-MenuCategory img {
    padding: 0 0 0 1rem;
    width: 2rem;
}

.MenuCategory .Content-MenuCategory {

    z-index: 100;
    
    background-color: #F1F1F1;
    border: 1px solid var(--color-green-light);
    top: 5.5rem;

    font-size: 5rem;


    display: flex;
    flex-direction: column;


    position: fixed;
}

.MenuCategory .Content-MenuCategory a {
    border-bottom: 1px solid var(--color-green-light);
    padding: 1rem 5rem;
    width: auto;
}
