.ModalAddBook {
  padding: 2rem 3rem; 
}
.ModalAddBook .form {
  padding-bottom: 1.5rem;
  display: flex;
  flex-direction: row;
}

.ModalAddBook .form .description {
  display: flex;
  flex-direction: column;
}

.ModalAddBook .form .description  input {
  padding: .5rem 7rem .5rem 1rem;
  font-size: 15px;
  margin: 0 0 1rem 2.5rem;

  border: 1px solid rgba(76, 76, 76, .5);
}

.ModalAddBook .form .description  textarea {
  padding: 1rem 2.25rem 3rem 1rem;
  height: 100%;
  font-size: 15px;
  margin: 0 0 0 2.5rem;

  border: 1px solid rgba(76, 76, 76, .5);

  resize: none;
}

.ModalAddBook .form .MidiaSelection {
  display: flex;
  flex-direction: column;

  width: 400px;

  background-color: white;
  border: 1px solid rgba(76, 76, 76, .5);
  border-radius: 5px;
  
  margin-left: 1rem;
  
}
.ModalAddBook .form .MidiaSelection .Area-ViewsMedias{
  height: 250px;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-template-rows: 80px;
  overflow: scroll;
  overflow-x: hidden;
}

.ModalAddBook .form .MidiaSelection .Area-SelectMidias{
  background-color: #5ace5a;
  color: white;
  height: 35px;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.ModalAddBook .Footer {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-content: center;
}

.ModalAddBook .Footer .Limpar {
  width: 30rem;
  height: 5rem;

  color: rgba(76, 76, 76, .8);
  border: 1px solid rgba(76, 76, 76, .5);
  border-radius: 10px;

  font-size: 2rem;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;
}

.ModalAddBook .Footer .Limpar:hover {
  background-color: var(--color-red);
  color: var(--color-background);
}

.ModalAddBook .Footer .Salvar{
  width: 30rem;
  height: 5rem;
  
  background-color: var(--color-green-light);
  color: var(--color-background);

  border: none;
  border-radius: 10px;

  font-size: 20px;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;
}

.ModalAddBook .Footer .Salvar:hover {
  background-color: var(--color-green-dark);
}
