.ButtonModal-Selection-Midia {
  cursor: pointer;
  background-color: transparent;
  border: none;
  transition: .5s;
  padding: 1rem 5rem;
  font-size: 2rem;
  font-family: "Roboto";
  color: white;
}

.selection-ModalBase {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;

  background-color: rgba(0, 0, 0, .8);

  display: flex;
  justify-content: center;
  align-items: center;

}

.selection-ModalBase > .ContainerModalBase {

  overflow: hidden;

  border-radius: 10px;
  background-color: color;
  position: fixed;
  z-index: 0;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  box-shadow: 8px 8px 10px black;
}

.selection-ModalBase .Selection-headerModalBase {
  width: 90%;
  padding: 1rem;

  border-bottom: 1px solid rgba(76, 76, 76, 0.5);

  display: flex;
  justify-content: space-between;
}

.selection-ModalBase .Selection-headerModalBase button{
  padding: 0;
  margin: 0;

  border: 0;
}

.selection-ModalBase .Selection-headerModalBase button img{
  padding: 0;
  margin: 0;

  width: 30px;
}

.selection-ModalBase .Selection-headerModalBase span{
  font-size: 2rem;
  font-weight: 300;
  font-family: 'Inter';
}
.Content {
  width: 100%;
  height: 100%;
}










.SelectionMidias {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 3rem;
}

.SelectionMidias .Content {
  margin: 1.5rem auto;

}

.SelectionMidias .Content .Midias{
  border: 1px solid black;
  border-radius: 10px;
  width: 320px;
  height: 320px;
  margin: 1rem auto;
  background-color: white;
  margin-right: 2rem;
  margin-left: 2rem;
  padding: 2rem;
  overflow: auto;
}
.SelectionMidias .Content p{
  font-size: 2rem;
}

.SelectionMidias .Buttons{
  width: 300px;
  height: 6.5rem;

  margin-left: 45px;
  
  display: flex;
  justify-content: center;
  align-items: center;
  
  background-color: var(--color-green-light) !important;
  color: white;

  border-radius: 10px !important;

  font-size: 20px;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;
}