.AsideBar {
    background-color: var(--color-primary-black);
    height: 100vh;
    width: 20vw;
    position: fixed;
    z-index: 1;
}

.category {
    display: flex;
    flex-direction: column;
}

.category a {
    border-width: 0.1px 0 0 0;
    border-style: solid;
    border-color: rgba(193, 192,189,50%);

    padding: 1rem 0;
}
.category a:first-child {
    padding: 1rem 2rem;
}

.category a:first-child > span{
    display: flex;
    flex-direction: row;
}
.category a:first-child span > img{
    width: 20px;
    height: 20px;
}

.category a:last-child {
    border-width: 0.5px 0;
}

.category a p {
    margin: 0 0 0 2rem;

    display: flex;
    align-items: center;
    font-size: 2rem;
}
.category img {
    margin: 0 1rem 0 0;
}

.logo {
    width: 20vw;
    position: absolute;
    top: 93vh; 
}

@media (max-width: 768px) {
    .AsideBar {
        display: none;
    }
}

@media (max-width: 1020px) {
    .logo img {
        width: 15rem;
    }
}


@media (min-width: 1100px) {
    .AsideBar {
        height: 100%;
    }
}