.ButtonModal {
  background-color: transparent;
  cursor: pointer;
  border: none;
  font-size: 2rem;
  font-family: "Roboto";
  border-radius: 8px;
  width: 100%;
  margin: 1rem 0;
}

.ModalBase {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;

  background-color: rgba(0, 0, 0, .8);

  display: flex;
  justify-content: center;
  align-items: center;

}

.ModalBase .ContainerModalBase {

  overflow: hidden;

  border-radius: 10px;
  background-color: #F1F1F1;
  position: fixed;
  z-index: 0;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  box-shadow: 8px 8px 10px black;
}

.ModalBase .headerModalBase {
  width: 95%;
  padding: 1rem;

  border-bottom: 1px solid rgba(76, 76, 76, 0.5);

  display: flex;
  justify-content: space-between;
}

.ModalBase .headerModalBase button{
  padding: 0;
  margin: 0;

  border: 0;
}

.ModalBase .headerModalBase button img{
  padding: 0;
  margin: 0;

  width: 30px;
}

.ModalBase .headerModalBase span{
  font-size: 2rem;
  font-weight: 300;
  font-family: 'Inter';
}
.Content {
  width: 100%;
  height: 100%;
}