.ClienteBookView {
    display: flex;
    flex-direction: row;
    text-align: center;
    background-color: #f1f1f1;
    min-height: 100vh;
}

.ClienteBookView .Container .BookViewContent {
    width: 100vw;
}

.ClienteBookView .Container .BookViewContent .Header {
    display: flex;
    margin-top: 2rem;
}

.ClienteBookView h1 {
    font: 3.5rem 'Inter';
    margin-left: 45%;
}

.ClienteBookView hr {
    margin-top: 2rem;
}

.ClienteBookView .Container .BookViewContent .BookContent{
    display: flex;
    justify-content: center;
}

.ClienteBookView .Container .BookViewContent .BookContent .BookContainer {
    margin-top: 2rem;
    margin-left: 3rem;
    margin-right: 3rem;
}

.ClienteBookView .Container .BookViewContent .BookContent .BookContainer .book-midias{
    background-color: white;
    width: 550px;
    height: 200px;
    border: 1px solid rgba(0, 0, 0, .3);
    border-radius: 10px;
    padding: 1rem;

    display: flex;
    justify-content: flex-start;
    align-items: center;


    overflow: scroll;
    overflow-y: hidden;

    margin-top: 3rem;
}

.ClienteBookView .Container .BookViewContent .BookContent .BookContainer .book-midias img{
    width: 90px;
    height: 120px;
    border: 1px solid rgba(0, 0, 0, .2);
    border-radius: 15px;
    margin: 1rem;
}

.ClienteBookView .Container .BookViewContent .BookContent .BookContainer .book-midias img:first-child {
    margin-left: 2rem;
}

.ClienteBookView .Container .BookViewContent .BookContent .BookInfo {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 3rem;
    height: 80vh;
    display: flex;
    width: 40vw;
}

.ClienteBookView input {
    height: 3rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px;
    width: 100%;
}

.ClienteBookView textarea {
    padding: 1rem;
    height: 15rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px;
    resize: none;
    width: 100%;
}

.ClienteBookView .Container .BookViewContent .BookContent .BookInfo span {
    display: flex;
    justify-self: self-start;
    font-size: 1.8rem;
    font-weight: 400;
    margin-top: 2rem;
}

@media (max-width: 1224px) {
    .ClienteBookView .Container .BookViewContent .BookContent .BookContainer .book-midias{
        width: 450px;
        height: 200px;
        padding: 0.5rem;
    }
}

@media (max-width: 1024px) {
    .ClienteBookView .Container .BookViewContent .BookContent .BookContainer .book-midias{
        align-self: center;
    }
    .ClienteBookView .Container .BookViewContent .BookContent{
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .ClienteBookView .Container .BookViewContent .BookContent .BookContainer {
        margin-top: 2rem;
        margin-left: 8rem;
        margin-right: 2rem;
        align-self: center;
        justify-content: center ;
        display: flex;
        flex-direction: column;
    }
    .ClienteBookView .Container .BookViewContent .BookContent .BookInfo {
        display: flex;
        align-self: center;
        flex-direction: column;
        width: 60vw;
        height: 60vh;
    }
}

@media (max-width: 768px) {
    .ClienteBookView .Container {
        margin-left: 0;
    }
    .ClienteBookView .Container .BookViewContent .BookContent .BookInfo {
        display: flex;
        align-self: center;
        margin-left: 0;
        margin-right: 0;
        width: 90vw;
    }
    .ClienteBookView .Container .BookViewContent .BookContent .BookContainer .book-midias{
        width: 250px;
        height: 100px;
        padding: 0.5rem;
    }
    .ClienteBookView .Container .BookViewContent .BookContent .BookContainer {
        margin-left: 0;
        margin-right: 0;
    }
    .ClienteBookView h1 {
        margin-left: 35%;
    }
}