#page-login {
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url(../../Images/background-image.png);
    background-repeat: no-repeat;
    background-size: cover;
}

#page-login .container {
    min-width: 40%;
    min-height: 40%;
    background: var(--color-primary-black);
    flex-direction: column;
    padding: 4rem 2rem;

    border-radius: 1rem;
    display: flex;
}

#page-login .container .logo-container {
    display: flex;
    justify-content: center;
}

#page-login .container .logo-container img {
    width: 100%;
}

#page-login .form-container {
    width: 100%;
}

#page-login .container .forgot-password{
    padding: 5px;
}

#page-login .container .input-block {
    display: flex;
    justify-content: center;
}

#page-login .container .input-block input {
    width: 70%;
    height: 3rem;
    margin-top: 2rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px
}

#page-login .container .button {
    margin-top: 2.5rem;
    width: 50%;
    padding: 1rem;
    font: 400 2.5rem Roboto;
    background: var(--color-green-light);
    color: white;
    border-radius: .8rem;
    border: none;

    text-align: center;
    transition: 0.5s;
}

#page-login .container .button:hover {
    background: var(--color-green-dark);
    text-decoration: none;
    cursor: pointer;
}

#page-login .container a {
    font-size: 1.5rem;
    margin-top: 0.8rem;
    transition: 0.5s;
}

#page-login .container a:hover {
    text-decoration: underline;
}