.BookProduto {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.BookProduto .BookCover {
  display: flex;
  align-items: center;
}

.BookProduto .BookCover img{
  width: 150px;
  height: 200px;
}

.BookProduto .BookCover .ImgOne{
  box-shadow: -2px -2px 5px gray;
}

.BookProduto .BookCover .ImgTwo {
  margin-left: -140px;
  margin-top: 20px;
  box-shadow: -2px -2px 5px gray;
}

.BookProduto .ImgThree {
  margin-left: -140px;
  margin-top: 40px;
  box-shadow: -2px -2px 5px gray;
}

.BookProduto .ItemsNumber {
  background-color: var(--color-background);
  border: 2px solid var(--color-green-light);
  border-radius: 50%;
  padding: 0.6rem;
  margin-left: -32px;
  margin-bottom: 170px;
}

.BookProduto .ItemsNumber span {
  font-size: 2rem;
}

.BookProduto p{
  max-width: 200px;
  text-align: start;
  color: black;
  font: 500 1.8rem 'Roboto';
  word-break: break-all;
  height: 15px;
  padding-top: 5px;
}

@media (max-width: 1400px) {
  .BookProduto .BookCover img{
    width: 120px;
    height: 180px;
  }
  
  .BookProduto .BookCover .ImgOne{
    box-shadow: -2px -2px 5px gray;
  }
  
  .BookProduto .BookCover .ImgTwo {
    margin-left: -110px;
    margin-top: 20px;
    box-shadow: -2px -2px 5px gray;
  }
  
  .BookProduto .ImgThree {
    margin-left: -110px;
    margin-top: 40px;
    box-shadow: -2px -2px 5px gray;
  }
  
  .BookProduto .ItemsNumber {
    background-color: var(--color-background);
    border: 2px solid var(--color-green-light);
    border-radius: 50%;
    padding: 0.6rem;
    margin-left: -25px;
    margin-bottom: 170px;
  }
  
  .BookProduto .ItemsNumber span {
    font-size: 1.5rem;
  }
}



