.ProductViewCliente {
    display: flex;
    flex-direction: row;
    text-align: center;
    background-color: #f1f1f1;
}

.ProductViewCliente .Container .ProductViewContent .Header {
    display: flex;
    margin-top: 2rem;
    width: 100vw;
}

.ProductViewCliente h1 {
    font: 3.5rem 'Inter';
    margin-left: 45%;
}

.ProductViewCliente hr {
    margin-top: 2rem;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo input {
    height: 3rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px
}

.ProductViewCliente select {
    font-size: 2rem;
    border-radius: 0;
    overflow: hidden;
    width: 45%;
    height: 4rem;
    border-radius: 0.8rem;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent {
    width: 75vw;
    display: flex;
    flex-direction: row;
    margin-left: 15%;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent .ProductContainer {
    width: 35vw;
    margin-right: 3rem;
    margin-top: 2rem;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo span {
    display: flex;
    justify-self: self-start;
    font-size: 1.8rem;
    font-weight: 400;
    margin-top: 2rem;
    margin-left: 2rem;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo hr{
    margin-top: 2rem;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo h2 {
    font: 2rem 'Inter';
    font-weight: 600;
    align-self: flex-start;
    margin-top: 2rem;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo > select {
    width: 30vw;
    margin: 1rem 0;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 3rem;
    width: 30vw;
    height: 100vh;
    display: flex;
}

.ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo .CategoriaContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media (max-width: 1800px) {
    .ProductViewCliente select {
        width: 45%;
    }
}

@media (max-width: 1024px) {
    .ProductViewCliente .Container .ProductViewContent .ProductContent {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-items: center;
        margin-left: 0;
        width: 100vw;
    }
    .ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo {
        display: flex;
        justify-content: center;
        flex-direction: column;
        height: 80vh;
        width: 90%;
    }
    .ProductViewCliente h1 {
        font-size: 3rem;
        margin-left: 25%;
    }
    .ProductViewCliente .Container .ProductViewContent .ProductContent .ProductInfo select {
        width: 100%;
    }
}