* {
    font-size: 60%;
}

#page-register {
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url(../../Images/background-image.png);
    background-repeat: no-repeat;
    background-size: cover;
}

#page-register .container {
    min-width: 40%;
    min-height: 40%;
    background: var(--color-primary-black);
    flex-direction: column;
    padding: 4rem 2rem;
    align-items: center;
    border-radius: 1rem;
}

#page-register .container .logo-container {
    display: flex;
    justify-content: center;
}

#page-register .form-container {
    width: 100%;
}

#page-register .form-container .user-name{
    display: flex;
    justify-content: center;
}

#page-register .form-container .user-name .input-block{
    width: 47.4%;
}

#page-register .container .input-block {
    display: flex;
    justify-content: center;
}

#page-register .container .input-block input {
    width: 90%;
    height: 4rem;
    margin-top: 3rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px
}

#page-register .container .button-container {
    display: flex;
    justify-content: center;
}

#page-register .container .button {
    margin-top: 2.5rem;
    width: 50%;
    padding: 1rem;
    font: 400 2.5rem Roboto;
    background: var(--color-green-light);
    color: white;
    border-radius: .8rem;
    border: none;
    text-align: center;

    transition: 0.5s;
}

#page-register .container .button:hover {
    background: var(--color-green-dark);
    text-decoration: none;
    cursor: pointer;
}

@media (max-width: 350px) {
    #page-register .container .button {
        width: 50%;
        font-size: 1.5rem;
    }
    #page-register .container .input-block input {
        margin-top: .5rem;
        height: 2rem;

    }
}





