.MidiaView {
    flex-direction: row;
    text-align: center;
    background: #f1f1f1;
    height: 100vh;
}

.MidiaView .Container {
    margin-left: 20vw;
}

.MidiaView .Container .MidiaViewContent .Header {
    display: flex;
    margin-left: 2rem;
}

.MidiaView .Container .MidiaViewContent .Header a {
    margin-top: 2rem;
}

.MidiaView h1 {
    font: 3.5rem 'Inter';
    margin-left: 45%;
    margin-top: 2rem;
}

.MidiaView hr {
    margin-top: 2rem;
    margin-left: 2rem;
}

.MidiaView .Container .MidiaViewContent .MidiaContent{
    display: flex;
    flex-direction: row; 
    justify-content: center;
    margin-left: 5rem;

}

.MidiaView .Container .MidiaViewContent .MidiaContent .ImageContainer {
    margin-top: 2rem;
    margin-left: 3rem;
    margin-right: 3rem;
}

.MidiaView .Container .MidiaViewContent .MidiaContent .MidiaInfo {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    margin-left: 3rem;
    width: 40vw;
}

.MidiaView .Container .MidiaViewContent .MidiaContent .MidiaInfo .button{
    margin: 2rem 14rem 0;
}

.MidiaView input {
    height: 3rem;
    border-radius: 0.8rem;

    font: 2rem Roboto;
    padding-left: 1rem;
    padding: 6px;
    width: 90%;
}

.MidiaView .Container .MidiaViewContent .MidiaContent .MidiaInfo span {
    display: flex;
    justify-self: self-start;
    font-size: 1.8rem;
    font-weight: 400;
    margin-top: 2rem;
}

.MidiaView .Container .MidiaViewContent .MidiaContent .MidiaInfo .button {
    margin-top: 5rem;
    margin-bottom: 5rem;
    width: 40%;
    font: 400 2rem Roboto;
    background: var(--color-green-light);
    color: white;
    border-radius: .8rem;
    border: none;
    transition: .5s;
}

.MidiaView .Container .MidiaViewContent .MidiaContent .MidiaInfo .button:hover {
    background-color: var(--color-green-dark);  
}

.MidiaView .button:hover {
    background: var(--color-green-dark);
    text-decoration: none;
}

@media (max-width: 1024px) {
    .MidiaView {
        height: 100%;
    }

    .MidiaView .Container .MidiaViewContent .MidiaContent{
        display: flex;
        flex-direction: column;
        margin-left: 0;
    }
    .MidiaView .Container .MidiaViewContent .MidiaContent .MidiaInfo .button{
        margin: 2rem 1rem 0;
    }
}

@media (max-width: 768px) {

    .MidiaView .Container {
        margin-left: 0;
    }
    .MidiaView .Container .MidiaViewContent .MidiaContent .MidiaInfo {
        width: 85vw;
        margin-left: 2rem;
        margin-bottom: 50px;
    }

    .MidiaView .Container .MidiaViewContent .MidiaContent .MidiaInfo .button{
        margin: 2rem 1rem 0;
    }
    .MidiaView input {
        width: 100%;
    }
    .MidiaView .Container .MidiaViewContent .Header {
        display: flex;
        margin-left: 0;
    }
    .MidiaView hr {
        margin-left: 0;
        width: 100%;
    }
    .MidiaView h1 {
        margin-left: 40%;
        font-size: 3rem;
    }
    .MidiaView  {
        display: flexbox;
    }
    .MidiaView .button{
        display: none;
    }
}