.LandingPage {
    display: flex;
    flex-direction: row;
    text-align: center;
    overflow-x: hidden;
    background-color: #f1f1f1;
}
  
.LandingPage .Container {
    margin-left: 20vw;
}

.LandingPage .Container .Top-Menu {
    position: fixed;
    top: 0;
    width: 80vw;
    background-color: white;
}

.LandingPage .Container .Top-Menu .CategoryMenu {
    display: flex;
    justify-content: space-between;
}

.LandingPage .Container .Top-Menu .CategoryMenu .sidebar {
    display: none;
}

.LandingPage .Container .Content-App {
    min-height: 100vh;
    background-color: #f1f1f1;
    width: 80vw;
}

.LandingPage .Container .Content-App .content-midia-views {
    padding: 150px 0 0;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2,220px);
}

.LandingPage .Container .Content-App .content-midia-views img {
    width: 150px;
    height: 200px;
    border-radius: 10px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    margin-bottom: 2rem;
}

.LandingPage .Container .Content-App .Content-Pagination{
    margin-left: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f1f1f1;
    padding: 5px 0;

    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}

@media (max-width: 1024px) {
    .LandingPage .Container .Content-App .content-midia-views {
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2,220px);
        margin-bottom: 3rem
    }
}

@media (max-width: 860px) {
    .LandingPage .Container .Content-App .content-midia-views {
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2,220px);
        padding: 0 0 9rem 0;
        margin-bottom: 0;
    }
}

@media (max-width: 768px) {
    .LandingPage .Container .Content-App .Content-Pagination {
        margin-left: 0;
    }
    .LandingPage .Container .Content-App {
        height: 100vh;
        width: 100vw;
    }
    .LandingPage .Container .Content-App {
        margin-top: 70px;
    }
    .LandingPage .Container {
        margin-left: 0;
    }
    .LandingPage .Container .Top-Menu .OrderMenu {
        display: none;
    }
    .LandingPage .Container .Top-Menu  {
        width: 100vw;
    }
    .LandingPage .Container .Top-Menu .CategoryMenu .sidebar {
        display: flex;
    }
}

@media (max-width: 500px) {
    .LandingPage .Container .Content-App .content-midia-views {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(2,220px);
    }
}


