.ProductsPage {
  display: flex;
  flex-direction: row;
  text-align: center;
}

.ProductsPage .Container {
  margin-left: 20vw;
}

.ProductsPage .Container .Top-Menu {
  position: fixed;
  width: 80vw;
  background-color: white;
  border-bottom: 2px solid rgba(114, 111, 111, 30%);
}

.ProductsPage .Container .Top-Menu .CategoryMenu {
  display: flex;
  justify-content: space-between;
}

.ProductsPage .Container .Content-App {
  min-height: 100vh;
  background-color: #f1f1f1;
  width: 80vw;
}

.ProductsPage .Container .Content-App .content-midia-views {
  padding: 10rem 0 0;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-template-rows: 260px;
}

.ProductsPage .Container .Content-App .Content-Pagination{
  margin-left: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
  padding: 10px 0;

  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.ProductsPage .aside-bar{
  display: none;
}

@media (max-width: 1440px) {
  .ProductsPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(250px, auto);
  }
}

@media (max-width: 870px) {
  .ProductsPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(250px, auto);
  }
}

@media (max-width: 768px) {
  .ProductsPage .Container .Top-Menu{
    width: 100vw;
  }
  .ProductsPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(250px, auto);
    width: 100vw;
    padding: 10px 0 9rem 0;
  }
  .ProductsPage .Container {
    margin-left: 0;
  }
  .ProductsPage .Container .Top-Menu .CategoryMenu {
    margin-left: 0;
  }
  .ProductsPage .Container .Content-App {
    margin-top: 70px;
    margin-left: 0;
    width: 100vw;
  }
  .ProductsPage .Container .Content-App .Content-Pagination {
    margin-left: 0;
  }
}

@media (max-width: 600px) {
  .ProductsPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(250px, auto);
  }
}

@media (max-width: 420px) {
  .ProductsPage .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(1, 1fr);
    grid-auto-rows: minmax(250px, auto);
  }
  .ProductsPage .Container .Content-App {
    margin-left: 10px;
  }
}