.ModalAddProduto {
  padding: 1.5rem 2rem;
}

.ModalAddProduto .Content{
  padding: 0 0 .5rem;
}
.ModalAddProduto .Content p{
  padding: 0;
  margin: 0 0 5px .6rem;

  font-size: 2.2rem;
  font-weight: 500;
}


.ModalAddProduto .Content .InfoData {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.ModalAddProduto .Content .InfoData input,
.ModalAddProduto .Content .InfoData select,
.ModalAddProduto .Content .InfoData select option {
  background-color: white;
  height: 30px;

  margin: .5rem .5rem;

  border: 1px solid rgba(76, 76, 76, .5);
  border-radius: 5px;
  width: 252px;

  font-size: 1.6rem;
  font-family: "Inter";
  font-weight: 400;

  padding-left: .5rem;
}

.ModalAddProduto .Content .InfoData input {
  width: 243px;
  padding-left: .7rem;
}

.ModalAddProduto .Content .SelectionForms{
  padding: .5rem 5px 1.5rem;
}

.ModalAddProduto .Content .ProdutoData {
  width: 520px;
  max-width: 580px;
  height: 150px;

  background-color: white;

  border: 1px solid rgba(76, 76, 76, .5);
  border-radius: 5px;

  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

}

.ModalAddProduto .Content .ProdutoData .Area-ViewsMedias{
  display: flex;
  align-items: center;
  width: 100%;
  height: 150px;
  overflow-y: hidden;
  overflow-x: scroll;
}

.ModalAddProduto .Content .ProdutoData .Area-SelectMidias{
  background-color: #5ace5a;
  color: white;
  height: 30px;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}


.ModalAddProduto .Footer {
  display: flex;
  width: 100%;
  align-self: center;
  justify-content: space-evenly;
}

.ModalAddProduto .Footer > button {
  width: 25rem;
  height: 5rem;

  color: rgba(76, 76, 76, .8);
  border: 1px solid rgba(76, 76, 76, .5);
  border-radius: 10px;

  font-size: 2rem;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;
}

.ModalAddProduto .Footer > button:hover {
  background-color: var(--color-red);
  color: var(--color-background);
}

.ModalAddProduto .Footer > button:last-child {
  width: 25rem;
  height: 5rem;
  
  background-color: var(--color-green-light);
  color: var(--color-background);

  border: none;
  border-radius: 10px;

  font-size: 20px;
  font-family: "Inter";
  font-weight: 500;

  cursor: pointer;
  transition: .5s;
}

.ModalAddProduto .Footer > button:last-child:hover {
  background-color: var(--color-green-dark);
}


/* slider CSS */
.swiper-container {
  width: 98%;
  height: 100%;
  z-index: 0;
}

.swiper-slide {
  font-size: 18px;
  background: #fff;

  width: 99%;
  height: 100%;
}



