.Accordion {
    display: flex;
    flex-direction: column;
    position: relative;

}

.Accordion .Title-Accordion {
    border-top: 1px solid rgba(193, 192,189,50%);
    padding: 1rem 2rem;
    display: flex;
    align-items: flex-end;
    cursor: pointer;
    font-size: 2rem;
}

.Accordion .Content-Accordion {
    position: relative;
    border-top: 1px solid rgba(193, 192,189,50%);

    display: flex;
    flex-direction: column;
    align-items: center;
}

.Accordion .Content-Accordion p {
    font-size: 2rem;
    padding: 1rem 2rem;
}