.ImgProduto {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ImgProduto img{
  width: 150px;
  height: 200px;
  border: 2px solid var(--color-green-light);
  border-radius: 0.8rem;
}

.ImgProduto p{
  max-width: 200px;
  height: 25px;
  text-align: center;
  /* overflow: hidden; */
  word-break: break-all;
  color: black;
  font-size: 1.7rem;
  font-weight: 600;
  padding: 0;
  margin-top: 5px;
}