.Search {
    padding: 1rem;
    display: flex;
    align-items: center;
}

.Search form {
    display: flex;
}

.Search form input {
    border: none;
    background-color: var(--color-background);
    font-size: 2rem;

    width: auto;
    padding-left: 2rem;
    border-radius: 10px 0px 0px 10px;

    outline: none;
    border-bottom: 1px solid rgba(114, 111, 111, 20%);
}

.Search form button {
    padding: .6rem;
    border: none;
    background-color: var(--color-background);
    border-bottom: 1px solid rgba(114, 111, 111, 20%);
    border-radius: 0 10px 10px 0;

}

.Search form button img {
    width: 25px;
    height: 25px;
}

@media (max-width: 768px) {
    .Search {
        padding: 1rem;
    }
    .Search form input {
        width: auto;
        font-size: 2rem;
        padding-left: 1rem;
    }
    .Search form button img {
        width: 20px;
        height: 20px;
    }
}

@media (max-width: 425px) {
    .Search form input {
        width: 120px;
        font-size: 2rem;
    }
}