.BookCliente {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.BookCliente .Container .Top-Menu .CategoryMenu {
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid rgba(114, 111, 111, 30%);
}

.BookCliente .Container .Content-App {
  background-color: #f1f1f1;
  width: 100vw;
  min-height: 90vh;
}

.BookCliente .Container .Content-App .content-midia-views {
  display: grid;
  grid-template-columns: repeat(5,1fr);
  grid-template-rows: 250px;
  align-items: center;
  justify-content: center;
}

.BookCliente .Container .Content-App .Content-Pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
  padding: 5px 0;

  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

@media (max-width: 1020px) {
  .BookCliente .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(3, 220px);
    grid-auto-rows: minmax(250px, auto);
    margin-bottom: 10rem;
  }
}

@media (max-width: 660px) {
  .BookCliente .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(2, 200px);
    grid-auto-rows: minmax(250px, auto);
  }
}

@media (max-width: 425px) {
  .BookCliente .Container .Content-App .content-midia-views {
    grid-template-columns: repeat(1, 200px);
    grid-auto-rows: minmax(250px, auto);
  }
}